@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;300;400;600&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'Poppins', sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


.wrapper_animation {
    background: #707070;
    width: 100%;
    padding: 25px 0;
}

video {
    /* object-fit: fill; */
    width: 100%;
}
.wrapper_dedicatedsection {
    position: relative;
    padding-bottom: 10%;
}


.stream_dedicated {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    height:100%;
    display: none;
}


.image_dedicated {
    position: relative;
    padding-top:20px;
    width:100%;
}

.wrapper_dedicatedsection__information {
    margin: 0 5%;
    text-align: left;
}

.wrapper_dedicatedsection__information--header {
    font-size: 49px;
    margin-top: 5%;
    display: flex;
    flex-direction: column;
}

.wrapper_dedicatedsection__information--icon {
    width:30%;
}

.wrapper_dedicatedsection__information--description {
    font-size: 14px;
    margin-top: 5%;
}

@media only screen and (min-width: 1366px)  { 

    .image_dedicated {
        width:auto;
    }

    .wrapper_dedicatedsection__information--header {
        font-size: 88px;
        margin-top: 1%;
        max-width: 510px;
    }

    .wrapper_dedicatedsection__information--icon {
        width:auto;
    }

    .wrapper_dedicatedsection__information--description {
        font-size: 25px;
        margin-top: 1%;
        max-width: 510px;
    }

    .stream_dedicated {
        display: block;
    }
    
}
.wrapper_dedicated_solution_section {
    background-color: #253140;
    color:#fff;
    padding:90px 10px;
}

.wrapper_dedicated_solution_section__header {
    font-size: 31px;
    padding:0 50px;
}

.wrapper_dedicated_solution_section__carousel {
    width:100%;
    margin: 0 auto;
    height: 710px;
    position: relative;
}

.dedicates_solution_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
}


.dedicates_solution_item:before {
    content:'';
    position: absolute;
    top:105% ;
    height:20px;
    width:100%;
    background:#e85a4f;
    opacity:1;
    background:radial-gradient(ellipse at center, rgba(0,0,0,0.35) 0% ,rgba(0,0,0, 0) 80%);
    -webkit-transform: translateY(5px);
            transform: translateY(5px);
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

.arrow_carousel {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    bottom: 50%;
    -webkit-transform: translate(0, 50%);
            transform: translate(0, 50%);
    position: absolute;
    z-index: 99;
}

.arrow_rotate {
    -webkit-transform: rotate(180deg);
            transform: rotate(180deg);
}

.arrow_box {
    cursor: pointer;
    transition: all .2s ease-in-out;
}

.arrow_box:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

@media only screen and (min-width: 1024px)  {  

    .wrapper_dedicated_solution_section__carousel {
        width:40%;
        margin: 0 auto;
        height: 710px;
        position: relative;
    }

    .wrapper_dedicated_solution_section__header {
        font-size: 50px;
        padding: 0px 40%
    }

    .arrow_carousel {
        width: 116%;
        left: -8%;
    }
    

}
button {
    outline: none;
    background-color: #fff;
    border:none;
    border: 1px solid #253140;
    font-size: 15px;
    border-radius: 30px;
    padding: 14px 38px;
    cursor: pointer;
    transition: 0.3s;
}

button:hover {
    color:#fff;
    background-color: #1E2834;
}

.second {
    font-weight: 600;
    color:#fff;
    background-color: #1E2834;
    font-size: 20px;

}

.second:hover {
    background-color: #fff;
    color:#1E2834;
}
.header {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 42px;
    position: relative;
}

.header_manage {
    display: flex;
    align-items: center;
    color:#fff;
    position: absolute;
    right: 42px;
}

.header_menu__mobile {
    position: absolute;
    right: 42px;
    cursor: pointer;
}

.header_language {
    font-weight: 600;
    font-size:16px;
    margin-left: 36px;
}

.header_language span {
    cursor: pointer;
}

span.seperator {
    background: #fff;
    width: 2px;
    height: 12px;
    display: inline-block;
    margin: 0 6px;
}


@media only screen and (max-width: 1024px)  {
    .header_manage {
        display: none;
    }

    .header_menu__mobile {
        display: flex;
    }

    .logo {
        width:50%;
    }
}

@media only screen and (min-width: 1024px)  {

    .header_manage {
        display: flex;
    }

    .header_menu__mobile {
        display: none;
    }

    .logo {
        width:auto;
    }

    .header {
        border-bottom: 1px solid #c4c4c4;
    }
}

.main_section {
    background-size: cover;
    background-image: 
    linear-gradient(
        rgba(30, 40, 52, 0.9),
        rgba(30, 40, 52, 0.9)
    ),
    url(/intrck/static/media/background.97c5e50f.png);
}

.main_section_button {
    padding: 100px 0 42px 0;
}

.main_section__text {
    color:#fff;
    margin: 0 auto 30px;
    text-align: center;
    max-width: 330px;
}

.arrow {
    cursor: pointer;
    margin-bottom: 30px;
}

@media only screen and (max-width: 1024px)  {
    .phones {
        width:100%;
    }
    .main_section__text {
        font-size: 28px;
        margin: 0 auto;
    }
    .arrow {
        display: none;
    }

    
}

@media only screen and (min-width: 1024px)  {

    .phones {
        width:auto;
    }


    .main_section__text {
        font-size: 50px;
        max-width: 580px;
        line-height: 60px;
    }

    .main_section_button {
        display: none;;
    }
}

.section_entries {
    position: relative;
}


.section_information {
    padding-top: 8%;
    padding-bottom: 6%;
}

.text_top {
    color:#253140;
    font-size: 25px; 
    display: flex;
    flex-direction: column;
    position: relative;
    background-color: white;
    margin: 0 4%;
}

.text_top__words {
    text-align: center;
}

.text_bottom {
    color:#C7CACE;
    font-size: 25px; 
    display: flex;
    flex-direction: column;
    margin:0 4%; 
    position: relative;
    background-color: white;
}

.text_bottom__words {
    display: flex;
    flex-direction: column;
    padding: 20px 0;
}

.text_bottom_small {
    font-size: 20px;
    margin-top: 30px;
    text-align: center;
}

.text_bottom_big {
    text-align: center;
}


.stream {
    background-size: cover;
    background-attachment: fixed;
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    display: none;
    height:100%;
}

.image {
    width: 100%;
    max-width: 1700px;
    position: relative;
    margin-top: 70px;
}



@media only screen and (min-width: 1366px)  {

    .stream {
        display: block;
    }

    .image {
        margin-top: 0;
    }

    .text_top {
        font-size: 35px; 
        align-items: flex-end;
    }

    .text_top__words {
        text-align: left;
    }

    .text_top,
    .text_bottom  { 
        background-color: transparent; 
        background-color: initial;
    }


    .text_bottom {
        color:#C7CACE;
        font-size: 35px; 
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        position: relative;
        background-color: transparent;
        background-color: initial;
    }
    
    .text_bottom__words {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        padding:0;
    }
    
    .text_bottom_small {
        font-size: 20px;
        margin-top: 30px;
        max-width:405px;
        text-align: right;
    }


    .text_bottom_big {
        width: 500px;
        text-align: right;
    }

}



@media screen and (min-width: 1500px){
    .text_top {
        margin:0 16%;
    }

    .text_bottom {
        margin:0 15%; 
    }
}
